@import '@wordpress/dataviews/build-style/style.css';

// Media query variables
$desktop: 1024px;
$tablet: 782px;
$mobile: 426px;

/* To fix rendering quirk in Chrome */
#wpwrap {
	background: #f1f1f1;
}

/* Hide WP notices on the settings page */
.toplevel_page_search-console,
.search-console_page_search-console-settings {
	.notice {
		display: none;
	}

	.star {
		color: #ffb900;
	}

	.red {
		color: red;
	}
}

.search-console.column-search-console {
	cursor: pointer;
}

.google-visualization-tooltip {
	padding: 2rem;
}

#search-console-wrapper {
	margin-left: -20px;

	@media (max-width: $tablet ) {
		margin-left: -10px;
	}

	hr {
		margin: 24px 0;
		border-top: 1px solid #eee;
	}

	.notice {
		display: block;
		margin-top: 15px;
	}

	.loading-settings {
		text-align: center;
		margin: 35vh 30px;
		display: flex;
		align-items: center;
		justify-content: center;

		.components-spinner {
			float: none;
		}

		.description {
			color: #777;
			margin-top: 4px;
		}
	}

	.masthead {
		background-color: #fff;
		padding: 15px 20px;
		box-shadow: rgb(0 0 0 / 10%) 0px 0px 0px 1px;
		display: flex;
		align-items: center;
		gap: 1rem;

		.masthead__branding {
			h1 {
				color: #222;
				font-size: 20px;
				font-weight: 400;
				display: flex;
				align-items: center;
				margin: 0;

				@media (max-width: $mobile ) {
					justify-content: center;
				}
			}

			svg {
				fill: #222;
				height: 40px;
				width: 40px;
				margin-right: 10px;
			}

			@media (max-width: $mobile ) {
				text-align: center;
			}
		}

		.masthead__plugin-links {
			font-size: 13px;
			display: flex;
			justify-content: flex-end;
			align-items: center;

			a {
				align-items: center;
				color: #23282d;
				cursor: pointer;
				display: inline-flex;
				gap: 4px;
				margin-left: 16px;
				text-decoration: none;

				svg {
					fill: currentColor;
					vertical-align: middle;
				}

				&:hover {
					color: #007cba;
					color: var(--wp-admin-theme-color);

					svg {
						fill: #007cba;
						fill: var(--wp-admin-theme-color);
					}
				}
			}

			@media (max-width: $tablet ) {
				a {
					margin-left: 8px;

					span {
						display: none;
					}
				}
			}

			@media (max-width: $mobile ) {
				justify-content: center;
				margin-top: 10px;
				width: 100%;
			}
		}

	}

	.footer {
		padding: 10px 30px;
		box-shadow: 0 0 0 1px #ccd0d4, 0 1px 1px 1px rgba(0, 0, 0, .04);
		text-align: center;
		margin-top: 2em;

		.dashicons-star-filled {
			color: #ffb900;
		}

		.inner-container {
			margin: 0 auto;
			max-width: 1024px;

			@media (max-width: $desktop ) {
				display: block;
			}
		}

		a {
			color: #555d66;
			display: inline-block;
			margin: 5px 8px;
			text-decoration: none;

			&:hover {
				color: #007cba;
				color: var(--wp-admin-theme-color);
			}
		}
	}

	/* General styles for all setting tabs */
	.search-console-settings {

		max-width: 1024px;
		margin: 1em auto;

	}

	.search-console-dashboard {
		margin-top: 1em;
		padding: 0 1rem;

		.search-console-modal-container {
			padding: 1em 0;
		}

		.search-console-filters {
			display: flex;
			justify-content: space-between;

			.search-console-filters-options {
				display: flex;
				gap: 1em;
			}
		}

		.search-console-table-bar {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 1 rem;

			input {
				width: 300px;
			}

			.search-console-dimensions {
				padding: 0;
				display: flex;
				flex: 1;

				button {
					padding: 1rem;
					cursor: pointer;
					font-weight: bold;
					border-bottom: 2px solid transparent;
				}

				.is-selected {
					border-bottom: 2px solid #135e96;
				}

				.components-button:focus:not(:disabled) {
					box-shadow: none;
				}
			}

		}

	}

	.ads-container__reviews {
		background-color: var(--wp-admin-theme-color);
		box-shadow: 0 0 0 1px var(--wp-admin-theme-color-darker-20), 0 1px 1px 1px rgb(0 0 0 / 4%);
		color: #fff;
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 0;
			background-color: var(--wp-admin-theme-color-darker-20);
			mask-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'><polygon points='12 17.7371778 5.81966011 21.5 7.49246652 14.4659011 2 9.75735421 9.2141911 9.1728642 12 2.5 14.7858089 9.1728642 22 9.75735421 16.5075335 14.4659011 18.1803399 21.5'></polygon></svg>");
			-webkit-mask-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'><polygon points='12 17.7371778 5.81966011 21.5 7.49246652 14.4659011 2 9.75735421 9.2141911 9.1728642 12 2.5 14.7858089 9.1728642 22 9.75735421 16.5075335 14.4659011 18.1803399 21.5'></polygon></svg>");
			mask-size: 200px;
			-webkit-mask-size: 200px;
			mask-repeat: no-repeat;
			-webkit-mask-repeat: no-repeat;
			mask-position: -100px;
			-webkit-mask-position: -100px;
		}

		>* {
			position: relative;
			z-index: 1;
		}

		a {
			color: #fff;
			display: block;
			margin-top: 10px;
			font-size: 1.1em;
		}

		h2 {
			color: #fff;
		}
	}

	.components-card {
		.message {
			margin: 1rem 0;
		}
	}

	.components-notice {
		margin: 1em;
	}

	.components-snackbar-list {
		bottom: 40px;
		padding-left: 16px;
		padding-right: 16px;
		position: fixed;
	}

	.slideshow-container {
		overflow: hidden;
		display: flex;
		background-color: #fff;
		flex-wrap: wrap;
		box-sizing: border-box;
	}

	.slide {
		height: 1px;
		transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
		max-width: 100%;
		box-sizing: border-box;
		word-wrap: break-word;
		padding: 1em;

		p {
			margin: 0;
		}

		&.active {
			height: auto;
		}
	}

}